<template>
  <div class="account">
    <ProfilePic/>
    <ii-key/>
    <ii-menu class="mobile-only" id="menu" @click.native="openSidebar"/>
    <ii-notifications />
    <!-- TODO mobile logout -->
    <span class="mobile-hidden" id="logout" @click="handleLogout">logout</span>
  </div>
</template>

<script>
import ProfilePic from "@/components/ProfilePic";
import {mapActions} from "vuex";

export default {
  name: "Menu",
  components: {ProfilePic},
  methods: {
    ...mapActions(['logout']),
    openSidebar() {
      this.$bus.$emit('open-sidebar');
    },
    handleLogout() {
      this.logout()
    }
  }
}
</script>

<style lang="scss" scoped>
  .account {
    display: flex;
    gap: 15px;
  }

  #logout {
    cursor: pointer;

    &:hover {
      color: $clr-primary-hover;
    }
  }
</style>
