<template>
  <div class="campaign">
    <template v-if="!subscribers && !settings">
      <el-row v-if="currentPage && currentPage.page" type="flex" :gutter="50" style="flex-wrap: wrap;">
        <el-col :span="24" style="margin-bottom: 20px; margin-right: 0; padding-right: 40px" class="team">
          <Team/>
          <!-- TODO implement page settings -->
          <Subscribers
              v-if="currentCampaign"
              @toggle-settings="settings = !settings"
              @toggle-subscribers="subscribers = !subscribers"/>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12">
          <div class="media-container">
            <div class="refresh">
              <ii-refresh/>
            </div>
            <!-- TODO hardcoded -->
            <img :src="currentPage.page.thumb || 'https://launchigloo.com/images/blank_page.png'"
                 alt="~@assets/dummy.png">
          </div>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24" :lg="12" :xl="12" class="editor-controls">
          <div class="campaign-name-input">
            <el-input v-model="form.pageName" :disabled="loading" placeholder="Aqua Landing Page" @blur="handleSave"/>
            <div class="editor-settings">
              <el-button id="edit" @click="editPage">
                <ii-edit/>
              </el-button>
              <!-- TODO popups too wide -->
              <div class="icons">
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference" @click="previewPage">
                    <ii-message/>
                  </el-button>
                  Preview
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference">
                    <ii-copy/>
                  </el-button>
                  Duplicate
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference" @click="zip">
                      <ii-cloud/>
                  </el-button>
                  Export Page as ZIP file. It will contain html, images, css and javascript files that you can upload to
                  your webserver
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <!-- todo @click="exportPageJson" -->
                  <el-button circle slot="reference">
                    <a
                        :href="`https://launchigloo.com/api/page/export-json-download?id=${currentPage.page.id}`
                        + (currentPage.page.code ?
                            '&code=' + currentPage.page.code :
                            '')">
                      <ii-graphs/>
                    </a>
                  </el-button>
                  Export Page Template. You can then import it by clicking on the button on bottom left
                </el-popover>
                <el-popover placement="top" width="200" trigger="hover"
                            popper-class="button-popup">
                  <el-button circle slot="reference">
                    <ii-cross/>
                  </el-button>
                  Delete page
                </el-popover>
              </div>
            </div>
          </div>
          <div class="url">
            <div class="def-pg-url">Default page url</div>
            <div class="url-builder">
              <div class="url-prefix">sample-url.igloopage.com/campaign-name/</div>
              <div class="url-input">
                <el-input v-model="form.pageUrl" :disabled="loading" placeholder="Page url"></el-input>
              </div>
            </div>
          </div>
          <div class="code">
            <el-input type="textarea" :rows="6" class="campaign-input-area" placeholder="Header Code"
                      v-model="form.header" :disabled="loading" @blur="handleSave"/>
            <el-input type="textarea" :rows="6" class="campaign-input-area" placeholder="Footer Code"
                      v-model="form.footer" :disabled="loading" @blur="handleSave"/>
          </div>
        </el-col>
      </el-row>
      <component :is="template" v-else/>
      <!-- TODO: no select component if no pages exist ? -->
    </template>
    <template v-else-if="subscribers && !settings">
      <component @toggle-subscribers="() => {
        subscribers = !subscribers
        settings = false
      }" :is="funnelEmails"/>
    </template>
    <template v-else>
      <component @toggle-settings="() => {
        settings = !settings
        subscribers = false
      }" :is="settingsPage"/>
    </template>
    <!--  TODO doesnt work  -->
  </div>
</template>

<script>
import Team from "@/components/Team";
import {mapActions, mapGetters} from "vuex";
import SelectTemplate from "@/components/SelectTemplate";
import Subscribers from "@/components/Subscribers";
import Funnel from "@/components/Funnel";
import PageSettings from "@/components/PageSettings";

export default {
  name: "Campaign",
  components: {Team, SelectTemplate, Subscribers},
  data: () => ({
    form: {
      pageName: '',
      header: '',
      footer: '',
      pageUrl: '',
    },
    subscribers: false,
    settings: false,
    funnelEmails: Funnel,
    settingsPage: PageSettings,
    loading: false,
    template: SelectTemplate,
  }),
  methods: {
    ...mapActions(['setCurrentCampaign', "setStepProperty", "saveCampaign", "saveHf", 'exportPage']),
    handleSave() {
      // TODO debounce
      this.setStepProperty({property: 'name', value: this.form.pageName})
      this.setStepProperty({property: 'header', value: this.form.header})
      this.setStepProperty({property: 'footer', value: this.form.footer})
      this.loading = true;
      this.saveCampaign().then(() => {
        // TODO: update local campaign
      }).catch(() => {
        // TODO: revert form back to campaign values
      }).finally(() => {
        this.loading = false
      })
    },
    editPage() {
      if (this.currentPage.page.new_builder == 1) {
        window.location.href = `https://app.launchigloo.com/?page=${this.currentPage.page.id}&token=${localStorage.getItem('token')}`
      } else {
        window.location.href = `https://launchigloo.com/#/page/${this.currentPage.page.id}`
      }
    },
    previewPage() {
      let code = this.currentPage.page.code
      window.location.href =
          `https://launchigloo.com/preview/${this.currentPage.page.id}`
          + (code ?
              '?c=' + code :
              '')
    },
    // todo add json / fix inconsistency
    zip() {
      this.exportPage().then((res)=> {
        if(res.success == 'true') {
        window.location.href = `https://launchigloo.com/api/page/export-download?id=${this.currentPage.page.id}` +
            (this.currentPage.page.code ?
                '&code=' + this.currentPage.page.code :
                '')
        }
      })
    }
  },
  computed: {
    ...mapGetters(['currentPage', "currentCampaign"]),
    // exportJsonUrl() {
    //   this.exportPage()
    //   return this.currentPage.page.new_builder == 1 ? 'export'... : 'export-json-download...
    // },
  },
  watch: {
    currentPage: {
      handler(v) {
        if (v) {
          this.form.pageName = v.name
          this.form.header = v.page.header
          this.form.footer = v.page.footer
          this.subscribers = false
          this.settings = false
        }
      },
      immediate: true,
      deep: true,
    }
  },
  created() {
    const currentId = this.$route.params.id;
    this.setCurrentCampaign(currentId);
  },
  beforeRouteUpdate(to, from, next) {
    this.setCurrentCampaign(to.params.id);
    next();
  },
}
</script>

<style lang="scss">
.campaign {
  .el-textarea__inner {
    border-radius: 10px !important;
    border-color: transparent !important;
  }

  #edit {
    transition: all ease 0.2s;

    &:hover {
      transform: scale(1.04);
    }
  }

  .icons {
    .el-button {
      display: flex;
      justify-content: center;
      align-items: center;

      transition: all ease 0.2s;

      &:hover {
        transform: scale(1.06) rotate(10deg);
      }
    }
  }

  .campaign-name-input {

    .el-input__inner {
      width: 100% !important;
      height: 43px !important;
      border-radius: 10px !important;
      border: none !important;
      padding: 0 15px !important;
      margin-bottom: 25px !important;
    }
  }


  #campaign-name {
    height: 80px;
  }

  .url {
    display: flex;
    flex-direction: column;
    gap: 16px;

    .def-pg-url {
      font-weight: 500;
      font-size: 16px;
      text-transform: uppercase;
      color: #637076;
    }

    &-builder {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      gap: 5px;
    }

    &-prefix {
      color: #C5C5C5;
    }

    &-input {

      flex-grow: 1;

      .el-input {
        height: 43px;

        &__inner {

          width: 100% !important;
          height: 43px !important;
          border-radius: 10px !important;
          border: none !important;
          padding: 0 15px !important;
        }
      }
    }
  }
}

.button-popup {
  background-color: $clr-black !important;
  color: white !important;
  border: none !important;

  .popper__arrow:after {
    border-top-color: $clr-black !important;
  }
}
</style>

<style lang="scss" scoped>

.icons {
  display: flex;
  gap: 7px;

  svg {
    width: 13px;
    height: 13px;
  }
}

.team {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.campaign {
  margin: 0 -25px;
  display: flex;
  justify-content: center;
  padding: 25px;
  font-size: 14px;
  font-weight: 400;
  max-width: 1200px;

  .el-row {
    padding-top: 0;
  }
}

.editor-controls {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
}

.media-container {
  width: 100%;
  height: fit-content;
  max-width: 720px;
  margin-bottom: 20px;
  position: relative;

  .refresh {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 10px;
    right: 10px;
    width: 27px;
    height: 27px;
    border-radius: 50%;
    background-color: $clr-primary;
    color: white;
    padding: 6px;
    cursor: pointer;
    transition: all ease 0.3s;

    &:hover {

      transform: rotate(20deg);
    }

    svg {
      color: white;
    }
  }

  img {
    width: 100%;
    height: 100%;
  }
}

.editor-settings {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: -15px;
  margin-bottom: 30px;

  .el-button {
    background-color: $clr-btn-primary;
    color: white;
    height: 34px;
    width: 95px;
    padding: 0;

    &:hover {
      background-color: $clr-btn-hover;
    }

    &.is-circle {
      width: 34px;
    }
  }
}

.campaign-input {
  width: 100%;
  height: 43px;
  border-radius: 10px;
  border: none;
  padding: 0 15px;
  margin-bottom: 25px;

  &-area {
    padding: 0;
    margin-bottom: 25px;
  }

  &:focus-visible {
    outline: none;
  }
}

.code {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
</style>
