import Vue from 'vue';
import VueRouter from 'vue-router';
import Dashboard from '../views/Dashboard.vue';
import Campaign from "@/views/Campaign";
import Campaigns from "@/views/Campaigns";
import CampaignsSearch from "@/views/CampaignsSearch";
import Settings from "@/views/Settings";
import AppLayout from "@/components/layouts/AppLayout";
import SettingsLayout from "@/components/layouts/SettingsLayout";
import Funnel from "@/views/Funnel";
import Contest from "@/views/Contest";

import ContestSpecialSidebar from "@/components/ContestSpecialSidebar";
import LoginPage from "@/views/LoginPage";
import LoginLayout from "@/components/layouts/LoginLayout";

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        component: AppLayout,
        children: [
            {
                path: '/',
                name: 'Dashboard',
                component: Dashboard,
            },
            {
                path: '/campaign/:id',
                name: 'Campaign',
                component: Campaign,
                meta: {
                    sidebar: true,
                }
            },
            {
                path: '/campaigns',
                name: 'Campaigns',
                component: Campaigns,
                meta: {
                    sidebar: true,
                }
            },
            {
                path: '/campaignsSearch',
                name: 'CampaignsSearch',
                component: CampaignsSearch,
            },
            {
                path: '/contest',
                name: 'Contest',
                component: Contest,
                meta: {
                    sidebar: ContestSpecialSidebar,
                }
            },
        ],
    },
    {
        path: '/',
        component: SettingsLayout,
        children: [
            {
                path: '/settings',
                name: 'Settings',
                component: Settings,
                meta: {
                    specialSidebar: true,
                }
            },
            {
                path: '/funnel',
                name: 'Funnel',
                component: Funnel,
                meta: {
                    specialSidebar: true,
                }
            }
        ]
    },
    {
        path: '/',
        component: LoginLayout,
        children: [
            {
                path: '/login',
                name: 'Login',
                component: LoginPage
            }
        ]
    }
];

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes,
});

export default router;
