<template>
  <div class="login">
    <div class="login-form">
      <div class="logo"><ii-logo-big/></div>
      <div class="info">LOGIN TO YOUR ACCOUNT BELOW</div>
      <div class="inputs">
        <el-input
            v-model="email"
            placeholder="Enter your username"
            name="email"
            class="input"
            @keyup.enter.native="handleLogin"/>
        <el-input
            v-model="password"
            placeholder="Enter your password"
            name="password"
            type="password"
            class="input"
            @keyup.enter.native="handleLogin"/>
      </div>
      <el-button :disabled="loading" v-loading="loading" type="info" id="login" @click="handleLogin">LOGIN TO IGLOO</el-button>
      <div class="forget"> <ii-lock/> Forgot your password? </div>
    </div>
  </div>
</template>

<script>
import {mapActions} from "vuex";

export default {
  name: "Login",
  data: () => ({
    email: '',
    password: '',
    loading: false,
  }),
  methods: {
    ...mapActions(['login']),
    handleLogin() {
      this.loading = true;
      this.login({email: this.email, password: this.password}).finally(() => {
        this.loading = false;
      })
    }
  }
}
</script>

<style lang="scss">
  .login {

    .el-input__inner {
      width: 100%;
      height: 50px !important;
      background-color: white;
      text-align: center;
    }
  }
</style>

<style lang="scss" scoped>
  .login {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    &-form {
      background-color: $clr-primary;
      border-radius: 15px;
      //margin: auto 30px;
      width: 550px;
      height: 420px;
      padding: 30px 50px;

      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      gap: 25px;

      .forget {
        color: #c5d6de;
        display: flex;
        justify-content: center;
        align-items: center;

        svg {
          width: 20px;
          height: 20px;
        }
      }

      .logo {

      }

      .info {
        font-size: 17px;
        color: white;
      }

      .inputs {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 20px;
      }

      #login {
        width: 100%;
        height: 50px;

        font-size: 20px;
        font-weight: 700;
      }
    }
  }
</style>
