<template>
  <div class="list" v-if="campaigns && campaigns.length > 0">
    <div class="row search">
      <div class="row-text search">Campaigns</div>
      <div class="row-controls search">
        <ii-search/>
        <input v-model="search" type="text" class="row-controls search" placeholder="Search Campaigns..."/>
      </div>
    </div>
    <div class="row split">
      <div class="row-text">Campaigns</div>
      <div class="row-controls">
        Options
      </div>
    </div>
    <div v-for="c in filteredCampaigns" :key="c.id" class="row" >
      <router-link :to="{name: 'Campaign', params: { id: c.id } }" slot="reference">
        <div class="row-text">{{ c.name }}</div>
      </router-link>
      <div class="row-controls icons">
        <el-popover placement="top" width="120" trigger="hover" popper-class="button-popup popup-search">
          <router-link :to="{name: 'Campaign', params: { id: c.id } }" slot="reference">
          <el-button circle class="option-btn" >
            <ii-edit-alt/>
          </el-button>
          </router-link>
          Edit Campaign
        </el-popover>
        <!-- TODO copy -->
        <el-popover placement="top" width="120" trigger="hover" content="Copy" popper-class="button-popup popup-search no-route">
          <el-button circle class="option-btn" slot="reference" @click="handleDuplicate(c)">
            <ii-copy-alt/>
          </el-button>
        </el-popover>
        <!-- TODO prompt delete -->
        <el-popover placement="top" width="120" trigger="hover" content="Delete" popper-class="button-popup popup-search no-route">
          <el-button circle class="option-btn" slot="reference" id="campaign-delete" @click="handleDeleteCampaign(c.id)">
            <ii-delete/>
          </el-button>
        </el-popover>
      </div>
    </div>
  </div>
  <div class="list no-campaigns" v-else><div class="row">Create your first Campaign in a sidebar on the left!</div></div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  data: () => ({
    search: '',
  }),
  computed: {
    ...mapGetters(['campaigns']),
    filteredCampaigns() {
      const s = this.search.toLowerCase();

      if (this.search) {
        return this.campaigns.filter(c => c.name.toLowerCase().includes(s));
      }

      return this.campaigns;
    }
  },
  methods: {
    ...mapActions(['deleteCampaign']),
    handleDuplicate(campaign) {
      alert(`duplicate ${campaign.name}`);
    },
    handleDeleteCampaign(campaignId) {
      this.deleteCampaign(campaignId)
    }
  }
}
</script>

<style lang="scss">
.button-popup {

  &.popup-search {
    padding: 6px 15px !important;
    text-align: center;

    &.no-route {
      transform: translateY(12px);
    }
  }
}
</style>

<style lang="scss" scoped>
.list {
  width: 100%;

  .row {
    display: flex;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    height: 84px;
    background-color: white;
    margin-bottom: 2px;

    color: #101010;

    padding-top: 0;
    padding-bottom: 0;
    padding-left: 50px;
    padding-right: 150px;

    a {
      text-decoration: none;
      color: #101010;

      &:hover {
        color: $clr-txt-nav;
      }
    }

    &.search {
      padding-right: 50px;
      margin-bottom: 0;
    }

    @media (max-width: $media-desktop) {
      padding-right: 50px;
    }

    &.split {
      height: 40px;
      background-color: $clr-bg-dark;
      color: $clr-grey-light;
      margin-bottom: 0;

      & > div {
        font-size: 14px;
      }
    }

    &-text {
      font-size: 16px;
      font-weight: 600;

      &.search {
        font-size: 24px;
        font-weight: 600;
        margin-right: 25px;
      }
    }

    &-controls {
      width: 100%;
      height: 39px;
      max-width: 100px;

      display: flex;
      align-content: center;
      justify-content: center;
      align-items: center;

      &.search {
        color: $clr-grey;
        background-color: $clr-bg;
        border-radius: 25px;
        max-width: 300px;
        outline: none;
        border: none;
        padding: 0 25px;
      }

      &.icons {
        color: $clr-grey;

        #campaign-delete {

          &:hover {
            background-color: $clr-red;
            color: white !important;
          }
        }

        .el-button {
          margin-right: 8px;
          background-color: transparent;
          transition: all ease 0.3s;

          &:hover {
            background-color: $clr-green;
            color: white !important;
          }
        }

        svg {

        }
      }
    }
  }
}
</style>
